.pisence__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}


.pisence__navbar-links {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.pisence__navbar-links_logo {
    margin-right: 2rem;
}

.pisence__navbar-links_logo img {
    width: 75px;
    height: 90px;
}

.pisence__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.pisence__navbar-links_container p,
.pisence__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}


.pisence__navbar-menu {
    display: none;
    margin-left: 1rem;
    position: relative;
}

.pisence__navbar-menu svg {
    cursor: pointer;
}

.pisence__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top:40px;
    right: 0;
    margin-top: 1rem;
    min-width: 180px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.pisence__navbar-menu_container p {
    margin: 1rem 0;
}

@media screen and (max-width : 1050px ){
    .pisence__navbar-links_container {
        display: none;
    }
    .pisence__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width : 700px) {
    .pisence__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width : 550px) {
    .pisence__navbar {
        margin: 2rem;
    }
    .pisence__navbar-menu-container {
        top: 20px;
    }
}